import { Component, OnInit } from '@angular/core';
import { routerTransition } from '../../router.animations';
import { UsersService } from '../../shared/services/users.service';
import { UserBlock } from '../../shared/model/userBlock';
import { TranslateService } from '@ngx-translate/core';


@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.css'],
  animations: [routerTransition()]

})
export class UsersComponent implements OnInit {
  data = [];
  pageNumbers: number[] = [];
  currentPage = 0;


  constructor(private usersSrvice: UsersService) {
    this.getAllUsers(this.currentPage);
  }

  getAllUsers(pageNumber: number = 0) {
    this.usersSrvice.getAll(pageNumber).subscribe((result) => {
      this.data = result.data;
      this.generatePageNumbers(result.pageCount);
      console.log('JSON Response = ', this.data);
    })

  }
  generatePageNumbers(pageCount: number) {
    this.pageNumbers = [];
    for (let i = 0; i < pageCount; i++) {
      this.pageNumbers.push(i);
    }
  }
  changeNumber(ind) {
    this.currentPage = ind;
    this.getAllUsers(this.currentPage);
  }
  prev() {
    this.changeNumber(this.currentPage - 1)
  }
  next() {
    this.changeNumber(this.currentPage + 1)
  }

  block(id: number) {

    this.usersSrvice.block(id).subscribe((result: any) => {
      this.getAllUsers(this.currentPage);
      console.log(result)
    })
  }


  unBlock(id: number) {
    this.usersSrvice.unBlock(id).subscribe((result: any) => {
      console.log(result)
      this.getAllUsers(this.currentPage);
    })
  }









  ngOnInit(): void { }

}
